/********************** 
* admin
***********************/
div.lnb-wrap-large {
    position: fixed;
    width: 250px; height: 100%;
    padding-left: 47px;
    background: #fff;
    z-index: 1;
    
    h1 {
        margin: 75px 0;
        width: 75px; height: 50px;
        background: url('../img/admin/admin_logo.png') 0 50% no-repeat;
        @include prefix($bgs, 75px 50px, webkit ms);
        text-indent: -9999px;
    }
    ul.navigation {

        li {

            a {
                font-size: 16px;
                color: #97a4b1;
                line-height: 33px;

                &:hover {
                    font-family: $inter-m;
                    font-size: 20px;
                    color: #28313b;
                }
                &.on {
                    font-family: $inter-m;
                    font-size: 20px;
                    color: #28313b;
                }
            }
        }
    }
    div.submenus {
        padding-top: 33px;

        & > a {
            display: inline-block;
            color: #28313b;
            font-family: $inter-b;
            font-size: 13px;
            line-height: 33px;
            padding-right: 26px;
            background: url('../img/admin/icon_menu_down.png') 100% 50% no-repeat;
            @include prefix($bgs, 18px 18px, webkit ms);

            &.on {
                background: url('../img/admin/icon_menu_up.png') 100% 50% no-repeat;
                @include prefix($bgs, 18px 18px, webkit ms);
            }
        }
        div.sub-show-hide {

            a {
                display: block;
                font-size: 16px;
                color: #97a4b1;
                line-height: 33px;

                &:hover {
                    font-family: $inter-m;
                    font-size: 20px;
                    color: #28313b;
                }
                &.on {
                    font-family: $inter-m;
                    font-size: 20px;
                    color: #28313b;
                }
            }
        }
    }
}

div.light-content {
    position: absolute;
    width: 100%;
    padding: 0px 110px 0 230px;

    h2 {
        padding: 14px 0 72px 0;
        font-family: $inter-b;
        font-size: 42px;
        color: #28313b;
        line-height: 32px;
    }
    p.paragraph {
        line-height: 26px;
        font-size: 16px;
        color: #28313b;
    }
    p.paragraph-rect {
        padding: 20px;
        border-radius: 6px;
        border: solid 1px rgba(0, 0, 0, 0.1);
        background-color: #f2f4f5;
    }
    div.basic-image {
        padding: 60px 0;
    }
    div.col-two-wrap {

        div.one {
            float: left;
            width: 47%;

        }
        div.two {
            float: left;
            width: 47%;
            margin-left: 6%;
        }
    }
    div.col-three-wrap {

        div.one {
            float: left;
            width: 32%;

        }
        div.two {
            float: left;
            width: 32%;
            margin-left: 2%;
            margin-right: 2%;
        }
        div.three {
            float: left;
            width: 32%;
        }
    }
    h3.tit {
        height: 32px;
        margin-bottom: 18px;
        font-family: $inter-b;
        font-size: 28px;
        color: #28313b;
    }
    span.thumb-span-text {
        display: block;
        font-size: 16px;
        color: #28313b;
        line-height: 26px;
        padding-top: 15px;
    }
    div.search-options {

        div.f {
            float: left;
            width: 60%;

            ul {

                li {
                    float: left;
                    width: 210px;
                    padding-right: 20px;

                    span.tit {
                        display: block;
                        line-height: 32px;
                        font-size: 13px;
                        font-family: $inter-m;
                        color: #28313b;
                    }
                    span.search-item {
                        position: relative;

                        a { 
                            position: absolute;
                            top: 3px; right: 10px;
                            width: 16px; height: 16px;
                            background: url('../img/admin/icon_search.png') 50% 50% no-repeat;
                            @include prefix($bgs, 16px 16px, webkit ms);
                            text-indent: -9999px;
                        }
                    }
                }
            }
        }
        div.s {
            float: right;
            width: 40%;
            text-align: right;
            padding-top: 40px;
            padding-right: 5px;

            a {
                display: inline-block;
                width: 20px; height: 20px;
                margin-left: 20px;
                text-indent: 9999px;

                &.light-items-1 {
                    background: url('../img/admin/icon_user.png') 50% 50% no-repeat;
                    @include prefix($bgs, 20px 20px, webkit ms);
                }
                &.light-items-2 {
                    background: url('../img/admin/icon_file.png') 50% 50% no-repeat;
                    @include prefix($bgs, 20px 20px, webkit ms);
                }
                &.light-items-3 {
                    background: url('../img/admin/icon_file_plus.png') 50% 50% no-repeat;
                    @include prefix($bgs, 20px 20px, webkit ms);
                }
                &.light-items-4 {
                    background: url('../img/admin/icon_write.png') 50% 50% no-repeat;
                    @include prefix($bgs, 20px 20px, webkit ms);
                }
                &.light-items-5 {
                    background: url('../img/admin/icon_printer.png') 50% 50% no-repeat;
                    @include prefix($bgs, 20px 20px, webkit ms);
                }
                &.light-items-6 {
                    background: url('../img/admin/icon_link.png') 50% 50% no-repeat;
                    @include prefix($bgs, 20px 20px, webkit ms);
                }
                &.light-items-7 {
                    background: url('../img/admin/icon_download.png') 50% 50% no-repeat;
                    @include prefix($bgs, 20px 20px, webkit ms);
                }
            }
        }
    }

    div.select-adding {
        width: 920px;
        padding: 24px 24px 34px 24px;
        border-radius: 8px;
        border: solid 1px rgba(0, 0, 0, 0.1);
        background-color: #ffffff;

        p.para {
            font-size: 16px;
            color: #28313b;
            padding-bottom: 22px;
            line-height: 20px;
        }
        div.inner-box {

            div.options {
                float: left;
                width: 250px;
                margin-right: 60px;

                &:nth-child(3n) {
                    margin-right: 0;
                }
                span.tit {
                    display: block;
                    font-size: 13px;
                    font-family: $inter-m;
                    color: #28313b;
                    padding-bottom: 2px;
                    border-bottom: 2px solid #28313b;
                }
                ul {

                    li {
                        border-bottom: 1px solid #bccad7;
                        padding: 10px 5px;
                        
                        span.f {
                            float: left;
                            font-size: 16px;
                            color: #28313b;

                            span.box {
                                display: inline-block;
                                width: 18px; height: 18px;
                                margin-right: 7px;
                                vertical-align: middle;

                                &.disk {
                                    background: url('../img/icons/icon_server.png') 50% 50% no-repeat;
                                    @include prefix(background-size, 18px 18px, webkit ms);
                                }
                                &.cloud {
                                    background: url('../img/icons/icon_cloud.png') 50% 50% no-repeat;
                                    @include prefix(background-size, 18px 18px, webkit ms);
                                }
                                &.copy {
                                    background: url('../img/icons/icon_copy.png') 50% 50% no-repeat;
                                    @include prefix(background-size, 18px 18px, webkit ms);
                                }
                                &.switch {
                                    background: url('../img/icons/icon_switch.png') 50% 50% no-repeat;
                                    @include prefix(background-size, 18px 18px, webkit ms);
                                }
                                &.router {
                                    background: url('../img/icons/icon_router.png') 50% 50% no-repeat;
                                    @include prefix(background-size, 18px 18px, webkit ms);
                                }
                                &.bridge {
                                    background: url('../img/icons/icon_bridge.png') 50% 50% no-repeat;
                                    @include prefix(background-size, 18px 18px, webkit ms);
                                }
                                &.storage {
                                    background: url('../img/icons/icon_cube.png') 50% 50% no-repeat;
                                    @include prefix(background-size, 18px 18px, webkit ms);
                                }
                                &.application {
                                    background: url('../img/icons/icon_app.png') 50% 50% no-repeat;
                                    @include prefix(background-size, 18px 18px, webkit ms);
                                }
                            }
                        }
                        span.radiobox-default {
                            float: right;
                        }
                    }
                }
            }
        }

        div.form-comp {
            padding-top: 20px;
            
            span.tit {
                display: block;
                padding-bottom: 7px;
                font-family: $inter-m;
                font-size: 13px;
                color: #28313b;
            }
            div.f {
                float: left;
                width: 48%;
            }
            div.s {
                float: right;
                width: 48%;
            }
            span.radio {
                float: left;
                padding-bottom: 7px;
                font-family: $inter-m;
                font-size: 13px;
                color: #28313b;
                margin-top: 6px;
            }
        }
        div.inner-box-left {
            float: left;
            width: 50%;
            border-right: 1px solid rgba(0, 0, 0, 0.1);
            padding: 24px 24px 34px 24px;
        }
        div.inner-box-right {
            float: right;
            width: 50%;
            padding: 24px 24px 34px 24px;
        }
    }

    div.tab-btn-wrap {
        position: relative;

        ul {
            width: 100%; 
            border-bottom: 2px solid #e8eef4;

            li {
                float: left;
                margin-bottom: -2px;

                a {
                    display: inline-block;
                    font-family: $inter-m;
                    font-size: 21px;
                    line-height: 26px;
                    padding: 11px 25px;
                    color: $dark;

                    &.on {
                        border-bottom: 2px solid #9678ff;
                    }
                }
            }
        }
        div.tab-btn-right {
            position: absolute;
            top: 3px; right: 0;

            a {
                margin-left: 12px;
            }
        }
    }

    div.btn-left {
        text-align: left;
        padding: 50px 0;

        a {
            margin-right: 20px;
        }
    }
    div.btn-center {
        text-align: center;
        padding: 50px 0;

        a {
            margin: 0 10px;
        }
    }
}
